// @ts-nocheck
// import 'tailwindcss/tailwind.css';
import './assets/css/tailwindcss.css';
// 
import tButton from './components/Base/tButton/';

// 
import tColorPicker from './components/DataElement/tColorPicker';

// 
const components = [
  tButton,
  tColorPicker
];

//  install  Vue  use 
const install = function (Vue) {
  // 
  if (install.installed) return;
  // 
  components.map(component => Vue.component(component.name, component));
};

// 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export default {
  //  install Vue.use() 
  install,
  // 
  tButton,
  tColorPicker
};
